import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { LoadingService } from 'src/app/services/loading.service';
import { ToastController, ModalController, LoadingController } from '@ionic/angular';
import { ToastService } from 'src/app/services/toast.service';
import { Observable } from 'rxjs';
import { DatePipe } from '@angular/common';
import { debounceTime, distinctUntilChanged, filter, map, startWith } from 'rxjs/operators';
import { TokenV2Page } from '../../token-v2/token-v2.page';
import { BranchComponent } from 'src/app/components/branch/branch.component';
import { TimeSlotsComponent } from 'src/app/components/time-slots/time-slots.component';
import { AddAccountComponent } from 'src/app/components/add-account/add-account.component';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { DenominationSlideComponent } from 'src/app/components/denomination-slide/denomination-slide.component';
import { TransactionDateComponent } from 'src/app/components/transaction-date/transaction-date.component';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MomentDateAdapter } from '@angular/material-moment-adapter';
import { MY_FORMATS } from '../forex-cash-v2/forex-cash-v2.page';

export interface People {
  name: String,
  age: Number,
};

@Component({
  selector: 'app-loan-disbursement-v2',
  templateUrl: './loan-disbursement-v2.page.html',
  styleUrls: ['./loan-disbursement-v2.page.scss'],
  providers: [
    { provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE] },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
  ],
})
export class LoanDisbursementV2Page implements OnInit {


  @ViewChild('dynamic', { read: ViewContainerRef }) private loanDsbViewRef: ViewContainerRef;
  @ViewChild("denom", { read: ElementRef }) denomButtonRef: ElementRef;

  public loanForm: FormGroup;
  productCode = 'LND';
  tokenOrigin = 'Mobile';
  isShow: boolean = true;
  currentUser: any;
  phoneNumber: string;
  customerId: string;
  currencies: any;
  routerData: any;
  frmToggle: boolean;
  animationState = 'out';
  editMode: boolean = false;
  chargeAmount: any = 5;
  selectedCurrency: any;
  selectedCountryCode: string;
  accountInfo: any;
  customerDetails: any;
  transAmount: any;
  filteredOptions: Observable<any[]>;
  currencyControl = new FormControl<string>('');

  custAccounts: any[] = [];
  selectedLoanAccount: any;
  filteredLoanAccount: any = [];
  isShowDenomError: boolean = false;
  isShowDisburseValid: boolean = false;
  homeIconToggle: boolean = false;
  paymentMode: string[] = ['Cash', 'Account', 'Cheque'];
  cheque_types: string[] = ['Banker Cheque', 'Demand Draft'];
  LOAN_TYPE: string[] = ['Home Loan', 'Vehicle Loan', 'Educational Loan'];
  selecteCHQOption = 'Banker Cheque';
  paymentModeSelected: string = 'Cash';
  loanDisbursementResponse: any;
  transactionId: any;
  filteredPaymentAccount: any;
  selectedPaymentAccount: any;
  loanAccounts: any = [];
  module = "Disbursement";
  transAmt: any;
  disbursementAmount: any;
  currentBalance: any;

  @ViewChild(MatAutocompleteTrigger) autocomplete: MatAutocompleteTrigger;

  constructor(private formBuilder: FormBuilder,
    private api: ApiService,
    private router: Router,
    private shareDataService: DataService,
    public loading: LoadingService,
    private cdr: ChangeDetectorRef,
    private modalController: ModalController,
    public toastService: ToastService,
    public datepipe: DatePipe) { }

  ngOnInit() {
    this.currentUser = this.shareDataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = this.currentUser?.customerId;
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));
    this.formInit();

    this.getLoanAccounts();
    this.fetchDisbursementAccInfo();
    this.loadData();

    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
      console.log("Values?", this.routerData);
      this.editMode = this.routerData?.data ? true : false;
    }

    if (this.editMode) {
      const data = this.routerData?.data;
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === data?.transactionCurrency);
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode = this.selectedCurrency?.countryCode.toLowerCase();
    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "INR");
      this.selectedCurrency = countryCode[0];
    }

    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
    })

    this.loanForm.get('transactionAmount')?.valueChanges.subscribe(val => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.filteredOptions = this.currencyControl.valueChanges.pipe(
      startWith(''),
      map((value: any) => {
        const currencyCode = typeof value === 'string' ? value : value?.currencyCode;
        return currencyCode ? this._filter(currencyCode as string) : this.currencies.slice();
      }),
    );

    let today = new Date().toISOString();
    const convertedDate = this.datepipe.transform(today, 'yyyy-MM-dd');
    this.loanForm.get('transactionDate').patchValue(convertedDate);
  }

  onScreenRefresh() {
    this.editMode = this.routerData?.data ? true : false;
    this.currentUser = this.shareDataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = this.currentUser?.customerId;
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    if (this.editMode) {
      const data = this.routerData?.data;
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === data?.transactionCurrency);
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode = this.selectedCurrency?.countryCode.toLowerCase();

    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "INR");
      this.selectedCurrency = countryCode[0];
    }

    this.formInit()
    this.loadData();

    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
    });

    this.loanForm.get('transactionAmount').valueChanges.subscribe(val => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.loanForm.get('transactionAmount')?.valueChanges.subscribe(val => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
        this.updateTransAmount();
        if (this.loanForm.get('paymentMethod')?.value == "Cash") {
          this.denomValidationListener();
        }
      }
    })
  }

  onScroll() {
    if (this.autocomplete?.panelOpen) {
      this.autocomplete.updatePosition();
    }
  }

  formInit() {
    this.loanForm = this.formBuilder.group({
      transactionId: [''],
      customerId: [''],
      productCode: [this.productCode, [Validators.required]],
      tokenOrigin: ['Mobile', [Validators.required]],
      loanAccountNumber: ['', Validators.required],
      accountNumber: [''],
      accountType: [''],
      accountPaymentNumber: [''],
      accountBalance: [''],
      accountCurrency: [''],
      denomination: [],
      transactionCurrency: ['', [Validators.required]],
      transactionAmount: ['', [Validators.required]],
      branchFlag: [true, [Validators.required]],
      accountBranch: [''],
      transactionDate: ['', [Validators.required]],
      transactionBranch: ['', [Validators.required]],
      isMobileTrans: [true, [Validators.required]],
      transactionTime: ['', [Validators.required]],
      totalChargeAmount: [this.chargeAmount, [Validators.required]],
      totalTransactionAmount: ['', [Validators.required]],
      paymentMethod: [''],
      chequeNumber: [''],
      chequeType: [''],
      chequeName: [''],
      exchangeRate: [''],
      accountAmount: [''],
      totalAmount: [''],
      remarks: ['']
    })
  }

  private _filter(currency: string): any[] {
    const filterValue = currency.toLowerCase();
    return this.currencies.filter(option => option.currencyCode.toLowerCase().includes(filterValue));
  }

  getLoanAccounts() {
    this.api.getLoanAccounts(this.phoneNumber, this.module).subscribe((res: any) => {
      if (res?.status == 200) {
        this.loanAccounts = res?.data.filter((account: any) => account?.disbursementComplete == false);
        this.loanForm.get('loanAccountNumber').patchValue(this.loanAccounts[0].loanAccountId);
      } else {
        this.loanAccounts = [];
        this.toastService.showToast(res?.message);
      }
    }, (err: any) => {
      this.toastService.showToast(err);
    })
  }

  fetchDisbursementAccInfo() {
    this.api.fetchDisbursementAccInfo(this.customerId).subscribe((res: any) => {
      if (res?.status == 200) {
        this.custAccounts = res?.data.reverse();
      } else {
        this.custAccounts = [];
        this.toastService.showToast(res?.message);
      }
    }, (err: any) => {
      this.toastService.showToast(err);
    })
  }

  loadData() {
    this.loading.present();
    this.api.custpomerDetails(this.phoneNumber).subscribe((resp) => {
      this.loading.dismiss();
      resp.custAccount = resp.custAccount.filter(card => card.status === "APPROVED");
      this.customerDetails = resp;
      this.upsertAccount(resp);
    }, (err: any) => {
      this.loading.dismiss();
    })
  }

  upsertAccount(filteredResponseSavingAccount: any) {

    if (this.editMode) {
      const data = this.routerData?.data;

      this.loanForm.get('transactionId')?.patchValue(data?.transactionId);
      this.loanForm.get('customerId')?.patchValue(data?.customerId);
      this.loanForm.get('loanAccountNumber')?.patchValue(data?.accountNumber);
      this.loanForm.get('accountNumber')?.patchValue(data?.accountNumber);

      this.loanForm.get('accountCurrency')?.patchValue(data?.accountCurrency);
      this.loanForm.get('transactionCurrency')?.patchValue(data?.transactionCurrency);
      this.loanForm.get('exchangeRate')?.patchValue(data?.exchangeRate);
      this.loanForm.get('denomination')?.patchValue(data?.denomination);

      let toInt = parseInt(data?.transactionAmount);
      let toLocale = toInt.toLocaleString('en-IN');
      this.loanForm.get('transactionAmount')?.patchValue(toLocale);

      let formatedAmount: any = this.shareDataService.formatCurrency(data?.totalTransactionAmount, data?.transactionCurrency);
      this.loanForm.get('totalTransactionAmount').patchValue(formatedAmount);
      this.loanForm.get('transactionBranch')?.patchValue(data?.transactionBranch);
      this.loanForm.get('transactionDate')?.patchValue(data?.transactionDate);
      this.loanForm.get('transactionTime')?.patchValue(data?.transactionTime);
      this.loanForm.get('remarks')?.patchValue(data?.remarks);

      // Mode Object TODO;

      if (data?.modeInfo.length != 0) {
        let modePayload = data?.modeInfo[0];
        this.loanForm.get('paymentMethod')?.patchValue(modePayload?.rePaymentMode);
        if (modePayload?.rePaymentMode === 'Account') {
          this.selectedPaymentAccount = modePayload?.accountNumber;
          this.loanForm.get('accountPaymentNumber')?.patchValue(modePayload?.accountNumber);
        } else if (modePayload?.rePaymentMode === 'Cheque') {
          this.selecteCHQOption = modePayload?.chequeType;
          this.loanForm.get('accountPaymentNumber')?.patchValue(modePayload?.accountNumber);
          this.loanForm.get('chequeType')?.patchValue(modePayload?.chequeType);
          this.loanForm.get('chequeName')?.patchValue(modePayload?.nameOnCheque);
        }
      }

    } else {
      this.selectedCountryCode = this.selectedCurrency.countryCode.toLowerCase();
      this.loanForm.get('transactionCurrency')?.patchValue(this.selectedCurrency.currencyCode);
      this.loanForm.controls.accountBranch.patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
      this.loanForm.get('transactionBranch').patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
    }
    this.cdr.markForCheck();
  }

  getChargeDetails() {
    this.api.getChargeDetails(this.accountInfo.accountId).subscribe(resp => {
      this.chargeAmount = resp?.[0]?.amount;
      this.loanForm.get('totalChargeAmount').patchValue(resp?.[0]?.amount);
    }, err => console.log('Error: ', err))
  }

  onLoanAccountSelect(e: any) {
    let filteredLoanAccount: any = this.loanAccounts?.filter(account => account?.loanAccountId === this.selectedLoanAccount);
    this.filteredLoanAccount = filteredLoanAccount[0];
    const countryCode = this.currencies.filter((i: any) => i.currencyCode === this.filteredLoanAccount?.accountCurrency);
    this.selectedCurrency = countryCode[0];
    this.selectedCountryCode = this.selectedCurrency?.countryCode.toLowerCase();
    this.loanForm.get('transactionCurrency')?.patchValue(this.filteredLoanAccount?.accountCurrency);
  }

  onPaymentAccountSelect(e: any) {

    let eAccountNumber = e.toString();
    this.selectedPaymentAccount = eAccountNumber;
    console.log("eAccountNumber", eAccountNumber)
    let filteredPaymentAccount: any = this.custAccounts?.filter((account: any) => account.accountNumber === eAccountNumber);
    this.filteredPaymentAccount = filteredPaymentAccount[0];
    console.log(this.filteredPaymentAccount)
  }

  paymentModeChange(event) {
    this.paymentModeSelected = event.value;

    if (this.paymentModeSelected == "Account") {
      this.loanForm.get('accountPaymentNumber').setValidators([Validators.required]); // Set Required Validator
      this.loanForm.get('accountPaymentNumber').updateValueAndValidity();

      this.loanForm.get('chequeType').clearValidators(); // 6. Clear All Validators
      this.loanForm.get('chequeType').updateValueAndValidity();
      this.loanForm.get('chequeName').clearValidators(); // 6. Clear All Validators
      this.loanForm.get('chequeName').updateValueAndValidity();

      if (!this.editMode) {
        this.selectedPaymentAccount = this.custAccounts[0].accountNumber;
        if (this.accountInfo.accountId != null) {
          this.loanForm.get('accountPaymentNumber').patchValue(this.accountInfo.accountId);
        } else {
          this.loanForm.get('accountPaymentNumber').patchValue(this.custAccounts[0].accountNumber);
        }
      }

    } else if (this.paymentModeSelected == 'Cheque') {
      this.loanForm.get('chequeType').setValidators([Validators.required]); // Set Required Validator
      this.loanForm.get('chequeType').updateValueAndValidity();
      this.loanForm.get('chequeName').setValidators([Validators.required]); // Set Required Validator
      this.loanForm.get('chequeName').updateValueAndValidity();

      this.loanForm.get('accountPaymentNumber').clearValidators(); // 6. Clear All Validators
      this.loanForm.get('accountPaymentNumber').updateValueAndValidity();

    } else {
      this.loanForm.get('accountPaymentNumber').clearValidators(); // 6. Clear All Validators
      this.loanForm.get('accountPaymentNumber').updateValueAndValidity();

      this.loanForm.get('chequeType').clearValidators(); // 6. Clear All Validators
      this.loanForm.get('chequeType').updateValueAndValidity();
      this.loanForm.get('chequeName').clearValidators(); // 6. Clear All Validators
      this.loanForm.get('chequeName').updateValueAndValidity();
    }
  }

  onBlurEvent(e: any) {
    this.updateTransAmount();
    if (this.loanForm.get('paymentMethod')?.value == "Cash") {
      this.denomValidationListener();
    }
  }

  submit(form: any) {
    if (this.loanForm.get('paymentMethod')?.value == "Cash") {
      if (this.isShowDenomError) {
        this.toastService.showToast("Denomination total should match with the transaction amount");
        return;
      }
      if (this.isShowDisburseValid) {
        this.toastService.showToast("Loan disbursement amount should not exceed than sanctioned amount");
        return;
      }
    }

    var date = new Date(form.transactionDate);
    let latest_date = this.datepipe.transform(date, 'yyyy-MM-dd');
    form.transactionDate = latest_date;
    form.customerId = this.customerId;
    form.productCode = this.productCode;
    form.accountType = this.filteredLoanAccount?.loanType;
    form.tokenOrigin = this.tokenOrigin;
    console.log(typeof(this.filteredLoanAccount?.accountCurrency));
    
    if(this.loanForm.get("transactionCurrency").value == "INR"){
      console.log(this.filteredLoanAccount?.accountCurrency);
      let ttam = this.loanForm.get('totalTransactionAmount').value;
      console.log(ttam.replace(/₹/g, ''));
    form.totalTransactionAmount = ttam.replace(/₹|,/g, '');
    }else{
      let ttam = this.loanForm.get('totalTransactionAmount').value;
      console.log(ttam);
      
      form.totalTransactionAmount = ttam.replace(/\D/g,'');
    }
    form.accountCurrency = this.filteredLoanAccount?.accountCurrency || "";
    form.isMobileTrans = true;
    form.holderName = this.filteredLoanAccount?.customerName;
    form.representativeInfo = [];

    // Total transaction convertion format
   
    let modeInfo: any;
    if (form?.paymentMethod === 'Account') {
      modeInfo = [
        {
          loanAccountNo: form?.loanAccountNumber,
          rePaymentMode: form?.paymentMethod,
          category: this.module,
          accountNumber: form?.accountPaymentNumber,
          accountCurrency: this.filteredPaymentAccount?.accountCurrency
        }
      ];
    } else if (form?.paymentMethod === 'Cheque') {
      modeInfo = [
        {
          loanAccountNo: form?.loanAccountNumber,
          rePaymentMode: form?.paymentMethod,
          category: this.module,
          accountCurrency: this.filteredPaymentAccount?.accountCurrency,
          chequeType: form?.chequeType,
          nameOnCheque: form?.chequeName,
        }
      ];
    } else {
      modeInfo = [
        {
          loanAccountNo: form?.loanAccountNumber,
          rePaymentMode: form?.paymentMethod,
          category: this.module,
        }
      ];
    }

    form.modeInfo = modeInfo;
    form.accountNumber = form?.loanAccountNumber;
    form.transactionAmount = form.transactionAmount.toString().replace(/,/g, '');
    form.tokenBranch = form.transactionBranch;

    if (this.editMode) {
      form.appointmentId = this.routerData.data.appointmentId;
      form.operation = 'update';
    }

    if (this.editMode) {
      this.api.appointmentBooking(form).subscribe((resp) => {
        localStorage.setItem("TransactionTime", resp.transactionTime);
        this.loanDisbursementResponse = resp;
        this.transactionId = this.loanDisbursementResponse.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        console.log('transactionId::', this.transactionId);
        if (this.loanDisbursementResponse === 200 || this.loanDisbursementResponse !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.onClick(this.loanDisbursementResponse);
          this.loanForm.reset();
        }
      });
    } else {
      this.api.cashDepositSave(form).subscribe((resp) => {
        localStorage.setItem("TransactionTime", resp.transactionTime);
        this.loanDisbursementResponse = resp;
        this.transactionId = this.loanDisbursementResponse.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        console.log('transactionId::', this.transactionId);
        if (this.loanDisbursementResponse === 200 || this.loanDisbursementResponse !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.onClick(this.loanDisbursementResponse);
          this.loanForm.reset();
        }
      });
    }
  }

  async onClick(event) {
    let modal = await this.modalController.create({
      component: TokenV2Page,
      componentProps: {
        value: event,
        screen: "onScreen"
      },
    });

    modal.onDidDismiss().then((res) => {
      this.routerData = res;
      console.log("Getting Back Data", this.routerData);
      this.onScreenRefresh();
    });

    return await modal.present();
  }

  enableDisableAction(type: any) {
    switch (type) {
      case 'TXAM':
        this.toggleFrm();
        break;
      default:
        console.log("No such type exists!");
        break;
    }
  }

  toggleFrm() {
    this.frmToggle = !this.frmToggle;
    this.animationState = this.animationState === 'out' ? 'in' : 'out';
    this.createComponent();
  }

  createComponent() {
    this.loanDsbViewRef.clear();
    let childComponentRef = this.loanDsbViewRef.createComponent(DenominationSlideComponent);
    let childComponent = childComponentRef.instance;
    childComponent.unique_key = "loanDsbViewRef";
    childComponent.denomData = this.loanForm.get('denomination')?.value;
    childComponent.loanDsbViewRef = this;
  }

  removeDynamicComponent(): void {
    this.loanDsbViewRef.clear();
  }

  remove(view: any) {
    console.log("view disiss?", view);
    let data = JSON.parse(localStorage.getItem(view)) || null;
    console.log("Data??", data);
    if (view == 'loanDsbViewRef') {
      this.loanDsbViewRef.clear();
      if (data != null && data.total != 0) {
        this.denomAmountValidation(data.total);
        this.loanForm.get('denomination')?.patchValue(data.denomination);
        console.log(this.loanForm.value);
      }
    }
  }

  formatAccountBalance(balance: any) {
    if (balance > 0) {
      let toInt = parseInt(balance);
      let toLocale = toInt.toLocaleString('en-IN', { style: "currency", currency: "INR" });
      return toLocale;
    } else {
      let toInt = parseInt('0');
      let toLocale = toInt.toLocaleString('en-IN', { style: "currency", currency: "INR" });
      return toLocale;
    }
  }

  denomAmountValidation(amount: any) {
    console.log("Amount", amount)
    if (Number(this.loanForm.get('transactionAmount')?.value?.split(',').join('')) != Number(amount)) {
      this.isShowDenomError = true;
      this.cdr.markForCheck();
      this.toastService.showToast("Denomination total should match with the transaction amount");
    } else {
      this.isShowDenomError = false;
      this.cdr.markForCheck();
    }

  }

  denomValidationListener() {
    let data = JSON.parse(localStorage.getItem('loanDsbViewRef')) || null;
    console.log("Data??", data);
    if (data?.title == 'loanDsbViewRef') {
      if (data != null && data.total != 0) {
        this.denomAmountValidation(data.total);
        console.log(this.loanForm.value);
      }
    }
  }


  balanceCheck(event: any) {
    this.transAmt = event.target.value;
    this.transAmt = this.transAmt.toString().replace(/,/g, '');
    let totalAmount = parseFloat(this.filteredLoanAccount?.sanctionedAmount) - parseFloat(this.filteredLoanAccount?.disbursedAmount);
    this.disbursementAmount = Number(totalAmount);

    if (parseFloat(this.disbursementAmount) < parseFloat(this.transAmt)) {
      this.isShowDisburseValid = true;
      this.toastService.showToast("Loan disbursement amount should not exceed than sanctioned/disbursed amount");
    } else {
      this.isShowDisburseValid = false;
      return;
    }
    this.cdr.markForCheck();
  }

  keyPressNumbers(event) {
    var charCode = (event.which) ? event.which : event.keyCode;
    // Only Numbers 0-9
    if ((charCode < 48 || charCode > 57)) {
      event.preventDefault();
      return false;
    } else {
      return true;
    }
  }

  getExChangeRate(currency) {
    this.api.getExchangeRate(this.accountInfo.accountCurrency).subscribe((res: any) => {
      const code = this.currencies.filter((x: any) => x.currencyCode == currency);
      let filteredCurrency = code[0].currencyCode;
      const rate = res?.rates?.[filteredCurrency];
      this.loanForm.get('exchangeRate').patchValue(rate);
      let toFix = this.loanForm.get('transactionAmount')?.value;
      console.log(toFix);
      console.log(this.loanForm.get('transactionAmount'));
      
      
      let val = (toFix != "undefined" || toFix != undefined) ? toFix?.replace(/,/g, '') : 0;
      if (!isNaN(val)) {
        let updatedAmount = (val * this.loanForm.get('exchangeRate')?.value) + this.chargeAmount;
        this.loanForm.get('totalTransactionAmount').patchValue(updatedAmount);
        if (!isNaN(updatedAmount)) {
          let formatedAmount: any = this.shareDataService.formatCurrency(updatedAmount, this.loanForm.get('transactionCurrency')?.value);
          this.cdr.markForCheck();
        }
      }
    });
    this.updateTransAmount();
  }

  updateTransAmount() {
    let toFix = this.loanForm.get('transactionAmount')?.value;
    console.log("check value", toFix);
    let val = (toFix != "undefined" || toFix != undefined) ? toFix?.replace(/,/g, '') : 0;
    console.log("check value after", val);
    if (!isNaN(val)) {
      let updatedAmount = (val * this.loanForm.get('exchangeRate')?.value) + this.chargeAmount;
      if (!isNaN(updatedAmount)) {
        let formatedAmount: any = this.shareDataService.formatCurrency(updatedAmount, this.loanForm.get('transactionCurrency')?.value);
        this.loanForm.get('totalTransactionAmount').patchValue(formatedAmount);
        this.cdr.markForCheck();
      }
    }
  }

  selectCurrencyCode(e: any) {
    console.log(this.loanForm);
    
    if (typeof e == "undefined" || typeof e == undefined || typeof e == null || typeof e == "object") {
      return;
    } else {
      if (e.length > 2) {
        const filteredCurrency = this.currencies?.filter((i: any) => i.currencyCode === e);
        this.selectedCountryCode = filteredCurrency[0].countryCode.toLowerCase();
        this.getExChangeRate(filteredCurrency[0].currencyCode);
        this.cdr.markForCheck();
      }
    }
  }



  getHomeBranch() {
    this.homeIconToggle = !this.homeIconToggle;
    if (this.homeIconToggle) {
      this.loanForm.get('transactionBranch')?.patchValue(this.customerDetails?.custAccount[0]?.accountBranch);
    } else {
      this.loanForm.get('transactionBranch')?.patchValue("");
    }
  }

  datePopup() {
    let oldDate = this.loanForm.get('transactionDate')?.value;
    this.modalController.create({
      component: TransactionDateComponent,
      componentProps: {
        date: this.loanForm.get('transactionDate').value,
      }
    }).then(modalResp => {
      modalResp.present()
      modalResp.onDidDismiss().then(res => {
        console.log(typeof res?.data);
        if (typeof res?.data == "undefined") {
          console.log("oldDate", oldDate);
          this.loanForm.get('transactionDate')?.patchValue(oldDate);
        } else {
          const convertedDate = this.datepipe.transform(res.data, 'yyyy-MM-dd');
          this.loanForm.get('transactionDate')?.patchValue(convertedDate);
        }
      })
    })
  }

  openPopup() {
    this.modalController.create({
      component: TimeSlotsComponent,
      componentProps: {
        date: this.loanForm.get('transactionDate').value,
      }
    }).then(modalResp => {
      modalResp.present()
      modalResp.onDidDismiss().then(res => {
        if (res.data != null || res.data != undefined || res.data != "undefined") {
          console.log(res);
          this.loanForm.get('transactionTime').patchValue(res.data?.form);
        }
      })
    })
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: BranchComponent,
      id: "branchModal",
      componentProps: {
      }
    });

    modal.onDidDismiss().then((modelData) => {
      if (modelData !== null) {
        this.loanForm.get('transactionBranch').patchValue(modelData.data['data'].branchName);
      }
    });

    return await modal.present();
  }

  goBack() {
    this.router.navigate(['dashboard']);
  }

  async addAccount() {
    const modal = await this.modalController.create({
      component: AddAccountComponent,
      id: "accountModal",
      componentProps: {
      }
    });

    modal.onDidDismiss().then((modelData) => {
      this.fetchDisbursementAccInfo();
    });

    return await modal.present();
  }

  ngOnDestroy(): void {
    localStorage.removeItem("loanDsbViewRef");
  }

}
